import React from "react";
import "./ChangePWD.css";
import { useContext, useState, useEffect } from "react";
import { FetchUrl } from "../../../store/communication";
import { useSearchParams, Link, useNavigate } from "react-router-dom";
import swal from "sweetalert";

function ChangePWD() {
  const url = `${useContext(FetchUrl)}`;
  const [inputs, setInputs] = useState({
    pwd: "",
    emailkey: "",
  });

  const navigate = useNavigate();
  const [searchParams, setSearchParams] = useSearchParams();
  useEffect(() => {
    setInputs((values) => ({
      ...values,
      emailkey: searchParams.get("emailKey"),
    }));
  }, []);

  const handleChange = (event) => {
    const name = event.target.name;
    const value = event.target.value;
    setInputs((values) => ({ ...values, [name]: value }));
  };
  const submitHandler = (event) => {
    console.log(
      JSON.stringify({
        name: inputs.name,
        nickName: inputs.nickName,
      })
    );
    event.preventDefault();
    fetch(url, {
      method: "POST",
      body: JSON.stringify({
        name: inputs.name,
        nickName: inputs.nickName,
      }),
      headers: {
        "content-type": "application/json",
      },
    })
      .then((response) => {
        console.log(response);
        return response.json();
      })
      .then((result) => {
        console.log(result);
        if (result.message === "FIND EMAIL FAIL") {
          swal(" ", "", "error");
        } else if (result.message === "FIND EMAIL SUCCESS") {
          swal(" : " + result.responseData.email, "", "success");
          navigate("/login");
          //////////////////////////// .
        } else {
          swal("", "", "error");
        }
      })
      .catch((err) => {
        swal("", "", "error");
      });
  };
  console.log(inputs);
  return (
    <div className="login">
      <div className="login-title">
        
        <div>
          <Link to="/FindPWD"> </Link>
        </div>
      </div>
      <form className="login-inputs" onSubmit={submitHandler}>
        <input
          className="input-box"
          type="text"
          name="name"
          onChange={handleChange}
          placeholder=" "
        />
        <input
          className="input-box"
          type="text"
          onChange={handleChange}
          name="nickName"
          placeholder=" "
          accept="number"
        />
        <button className="submit-btn"></button>
      </form>

      <div className="signup-btns">
        <Link to="/login">
          <div className="email">  </div>
        </Link>
      </div>
    </div>
  );
}

export default ChangePWD;
